.form-page {
  // width: 320px;
  // margin: 0 auto;

  @include font-style;
}

.form-page__head {
  width: 280px;
  margin: 0 auto;
}

.form-page__header {
  font-size: 36px;
  margin-top: 47px;
}

.form-page__text {
  margin-top: 35px;
  margin-bottom: 0;
  font-size: 14px;
  text-transform: uppercase;
}

// .form-page__wrapper {
//   font-size: 16px;
//   text-transform: uppercase;
//   margin-top: 39px;
// }

.form-page__wrapper {
  width: 280px;
  margin: 39px auto 0 auto;
  font-size: 16px;
  text-transform: uppercase;
  // @include margin-22;
}

@media (min-width: $tablet-width) {
  // .form-page {
  //   width: 768px;
  //   margin: 0 auto;
  // }

  .form-page__head {
    width: 708px;
    margin: 0 auto;
  }

  .form-page__header {
    font-size: 60px;
    font-weight: 400;
    margin-top: 29px;
  }

  .form-page__text {
    font-size: 20px;
    margin-top: 50px;
    line-height: 30px;
    padding-right: 252px;
    // margin-top: 50px;

    @include tablet-margin;
  }

  .form-page__wrapper {
    width: 708px;
    margin: 0 auto;
    padding-bottom: 37px;
  }

  .form-page__container {
    margin-top: 64px;
  }
}

@media (min-width: $desktop-width) {
  // .form-page {
  //   width: 1220px;
  //   margin: 0 auto;
  // }

  .form-page__head {
    width: 1220px;
    margin: 0 auto;
  }

  .form-page__wrapper {
    width: 1220px;
    margin: 0 auto;
  }

  .form-page__header,
  .form-page__text {
    margin-left: 0;
    margin-right: 0;
  }

  .form-page__header {
    margin-top: 47px;
  }

  .form-page__text {
    padding-right: 833px;
  }

  .form-page__container {
    display: flex;
    justify-content: space-between;
  }
}
